import React, { Component } from 'react'
import { Form, Input, Button, DatePicker, Avatar } from 'antd'
import styles from './index.module.less'
import { updateScrum } from '@apis/scrum'
import MarkdownEditor from '@uiw/react-markdown-editor'
import MarkdownPreview from '@uiw/react-markdown-preview'

type Props = {
  scrum: any
}
type State = {
  act: string
  isEditVisible: boolean
  editorHeight: number
}

export default class ScrumNextComponent extends Component<Props, State> {
  private actTemp = ''
  private bodyRef = null
  constructor(props) {
    super(props)
    const { scrum } = this.props
    this.actTemp = scrum.act || ''
    this.state = {
      act: scrum.act || '',
      isEditVisible: false,
      editorHeight: 500,
    }
  }
  componentDidMount() {}

  showEditor() {
    this.setState({
      isEditVisible: true,
    })
  }

  hideEditor() {
    this.setState({
      isEditVisible: false,
    })
  }

  updateSummary = (editor, data, value) => {
    this.actTemp = value
  }

  async saveAct() {
    await updateScrum(this.props.scrum._id, {
      act: this.actTemp,
    })
    this.setState({
      act: this.actTemp,
      isEditVisible: false,
    })
    this.actTemp = ''
  }

  getBodyRef(ref) {
    if (!ref || this.bodyRef) {
      return
    }
    this.bodyRef = ref
    this.setState({
      editorHeight: this.bodyRef.clientHeight - 220,
    })
  }

  render() {
    const { act, isEditVisible, editorHeight } = this.state
    return (
      <div
        className={`${styles['project-detail-group']}`}
        style={{ minWidth: 600 }}
        ref={(ref) => {
          this.getBodyRef(ref)
        }}
      >
        <div
          className={`${styles['project-detail-group-header']} ${styles['project-detail-group-header-act']}`}
        >
          
        </div>
        <div className={styles['project-detail-group-body']}>
          {isEditVisible ? (
            <div className={styles['act-editor']}>
              <MarkdownEditor height={editorHeight} value={act} onChange={this.updateSummary} />
              <div className="m-t-12">
                <Button
                  size="small"
                  type="primary"
                  onClick={() => {
                    this.saveAct()
                  }}
                >
                  
                </Button>
                <Button
                  className="m-l-12"
                  size="small"
                  onClick={() => {
                    this.hideEditor()
                  }}
                >
                  
                </Button>
              </div>
            </div>
          ) : (
            <div
              className={styles['act-preview']}
              onClick={() => {
                this.showEditor()
              }}
            >
              <MarkdownPreview source={act || ''} />
            </div>
          )}
        </div>
      </div>
    )
  }
}
