import { DeleteOutlined, EllipsisOutlined } from '@ant-design/icons'
import { deleteTask, getTaskDetail, updateTask } from '@apis/tasks'
import ChargerComponent from '@components/Charger'
import StateComponent from '@components/State'
import { STATE, STATE_MAP } from '@constants/state'
import { Button, Drawer, Dropdown, Menu, Tag } from 'antd'
import TextArea from 'antd/lib/input/TextArea'
import React, { Component } from 'react'
import styles from './index.module.less'
import MarkdownEditor from '@uiw/react-markdown-editor'
import MarkdownPreview from '@uiw/react-markdown-preview'

type Props = {
  taskId: string
  afterAction?: ($event) => void
}

type State = {
  taskDetail: any
  isTaskDetailVisible: boolean
  isTitleEditor: boolean
  isDescriptionEditor: boolean
}

export default class TaskDetailComponent extends Component<Props, State> {
  private editorRef = null
  private descriptionRef = null
  private summaryTemp = ''

  constructor(props) {
    super(props)
    this.state = {
      taskDetail: null,
      isTaskDetailVisible: false,
      isTitleEditor: false,
      isDescriptionEditor: false,
    }
  }

  componentDidMount() {
    const { taskId } = this.props
    this.getTask(taskId)
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.taskId !== this.props.taskId) {
      this.getTask(nextProps.taskId)
    }
  }

  async getTask(taskId) {
    const detail: any = await getTaskDetail(taskId)
    if (detail) {
      this.setState({
        taskDetail: detail,
      })
      this.summaryTemp = detail && detail.description ? detail.description : ''
    }
  }

  setVisible(isTaskDetailVisible) {
    this.setState({
      isTaskDetailVisible,
    })
  }
  showDescriptionEditor() {
    this.setState({
      isDescriptionEditor: true,
    })
    setTimeout(() => {
      this.descriptionRef && this.descriptionRef.focus()
    })
  }

  hideDescriptionEditor() {
    this.setState({
      isDescriptionEditor: false,
    })
  }

  showTitleEditor() {
    this.setState({
      isTitleEditor: true,
    })
    setTimeout(() => {
      this.editorRef && this.editorRef.focus()
    })
  }

  async saveTitle(e) {
    const task = await updateTask(this.state.taskDetail._id, {
      name: e.target.value,
    })
    this.setState({
      taskDetail: task,
      isTitleEditor: false,
    })
  }

  async saveDescription() {
    const task = await updateTask(this.state.taskDetail._id, {
      description: this.summaryTemp,
    })
    this.setState({
      taskDetail: task,
      isDescriptionEditor: false,
    })
  }

  updateSummary = (editor, data, value) => {
    this.summaryTemp = value
  }

  onCloseDetail = () => {
    this.setState({
      isTaskDetailVisible: false,
    })
  }

  async taskActions({ key }) {
    if (key === 'delete') {
      await deleteTask(this.state.taskDetail._id)
      this.onCloseDetail()
    }
    const { afterAction } = this.props
    afterAction && afterAction(null)
  }

  async taskStateActions({ key }) {
    const dd = await updateTask(this.state.taskDetail._id, {
      state: key,
    })
  }

  async changeCharger(charger) {
    const taskDetail = await updateTask(this.state.taskDetail._id, {
      charger,
    })
    if (taskDetail) {
      this.setState({
        taskDetail,
      })
    }
    const { afterAction } = this.props
    afterAction && afterAction(null)
  }

  async changeState(state) {
    const taskDetail = await updateTask(this.state.taskDetail._id, {
      state,
    })
    if (taskDetail) {
      this.setState({
        taskDetail,
      })
    }
    const { afterAction } = this.props
    afterAction && afterAction(null)
  }

  getEditor(ref) {
    this.editorRef = ref
  }

  getDescription(ref) {
    this.descriptionRef = ref
  }

  render() {
    const { taskDetail, isTaskDetailVisible, isDescriptionEditor, isTitleEditor } = this.state
    const detailMenu = (
      <Menu
        onClick={(val) => {
          this.taskActions(val)
        }}
      >
        <Menu.Item key="delete">
          <DeleteOutlined />
          
        </Menu.Item>
      </Menu>
    )

    return (
      <Drawer
        title={
          <div className={styles['drawer-header']}>
            <div className={styles.aside}>
              <StateComponent
                status={taskDetail?.state}
                callBack={($event) => {
                  this.changeState($event)
                }}
              />
            </div>
            <div className={styles.actions}>
              <Dropdown overlay={detailMenu}>
                <EllipsisOutlined />
              </Dropdown>
            </div>
          </div>
        }
        placement="right"
        width={1024}
        destroyOnClose={true}
        closable={false}
        onClose={this.onCloseDetail}
        visible={isTaskDetailVisible}
      >
        <div className={styles['detail-wrap']}>
          <div className={styles['detail-header']}>
            {!isTitleEditor ? (
              <div
                className={styles.title}
                onClick={() => {
                  this.showTitleEditor()
                }}
              >
                {taskDetail && taskDetail.name}
              </div>
            ) : (
              <div className={styles.title}>
                <TextArea
                  ref={(editor) => {
                    this.getEditor(editor)
                  }}
                  defaultValue={taskDetail && taskDetail.name ? taskDetail.name : ''}
                  onBlur={(e) => {
                    this.saveTitle(e)
                  }}
                  rows={2}
                />
              </div>
            )}
          </div>
          <div className={styles['detail-body']}>
            <div className={styles['detail-tools']}>
              <div className="m-r-12">
                <ChargerComponent
                  userId={taskDetail && taskDetail.charger ? taskDetail.charger._id : null}
                  callBack={($event) => {
                    this.changeCharger($event)
                  }}
                />
              </div>
            </div>
            <div className={styles['detail-row']}>
              <div className={styles['detail-row-label']}>
                <i className="cloud cloud-description"></i> 
              </div>
              <div className={styles['detail-row-main']}>
                {isDescriptionEditor ? (
                  <>
                    <div className={styles['description-editor']}>
                      <MarkdownEditor
                        height={300}
                        value={taskDetail && taskDetail.description ? taskDetail.description : ''}
                        onChange={this.updateSummary}
                      />
                    </div>
                    <div className="m-t-12">
                      <Button
                        size="small"
                        type="primary"
                        onClick={() => {
                          this.saveDescription()
                        }}
                      >
                        
                      </Button>
                      <Button
                        className="m-l-12"
                        size="small"
                        onClick={() => {
                          this.hideDescriptionEditor()
                        }}
                      >
                        
                      </Button>
                    </div>
                  </>
                ) : (
                  <div
                    className={styles.description}
                    onClick={() => {
                      this.showDescriptionEditor()
                    }}
                  >
                    <MarkdownPreview
                      source={
                        taskDetail && taskDetail.description ? taskDetail.description : ''
                      }
                    />
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </Drawer>
    )
  }
}
