import React, { Component } from 'react'
import { Form, Input, Button, DatePicker, Avatar, Popover, InputNumber } from 'antd'
import moment from 'moment'
import { HotTable } from '@handsontable/react'
import styles from './index.module.less'
import { updateScrum } from '@apis/scrum'
import store from '@stores/store'
import { meanBy, find, map } from 'lodash'
import { SelectMembersComponent } from '@components/index'

const { RangePicker } = DatePicker

type Props = {
  scrum: any
}
type State = {
  summaryDays: number
  participants: any
  scoreList: any
  score: number | string
}

export default class ScrumSummaryComponent extends Component<Props, State> {
  private summaryDays = 7
  private summaryRange: any = [moment().startOf('week'), moment().endOf('week')]
  private timelineDetail = [
    ['', '', '', '', '', '', '', ''],
    ['', '', '', '', '', '', '', ''],
  ]
  private colHeaders = ['-', '-', '-', '-', '-', '-', '-']
  private checkDetail = [
    ['', '', '', '', ''],
    ['', '', '', '', ''],
    ['', '', '', '', ''],
  ]
  constructor(props) {
    super(props)
    const { scrum } = this.props
    const { start, end, check, timeline } = scrum
    if (start && end) {
      this.summaryRange = [moment(start * 1000), moment(end * 1000)]
      this.getRangeArray(this.summaryRange)
    }
    timeline && (this.timelineDetail = timeline)
    check && (this.checkDetail = check)
    this.state = {
      summaryDays: this.summaryDays,
      participants: scrum.participants || [],
      scoreList: scrum.scoreList,
      score: scrum.scoreList ? meanBy(scrum.scoreList, 'score').toFixed(1) : '--',
    }
  }
  componentDidMount() {}
  getRangeArray(duration) {
    let from = moment(duration[0])
    const end = moment(duration[1])
    const dateArray = ['']
    const dataRange = []
    while (from <= end) {
      dateArray.push(from.format('YYYY-MM-DD'))
      dataRange.push('')
      from = from.add(1, 'days')
    }
    this.colHeaders = dateArray
    this.timelineDetail = [
      ['', ...dataRange],
      ['', ...dataRange],
    ]
    this.summaryDays = this.colHeaders.length
    this.setState({
      summaryDays: this.summaryDays,
    })
    this.forceUpdate()
  }
  async changeRange(dates, dateStrings) {
    const scrum = await updateScrum(this.props.scrum._id, {
      start: dates[0].format('X') * 1,
      end: dates[1].format('X') * 1,
    })
    scrum && this.getRangeArray(dates)
  }
  getSummaryRef(ref) {
    console.log(ref)
  }
  private ctxm = {
    items: {
      row_above: {
        name: '',
      },
      row_below: {
        name: '',
      },
      col_left: {
        name: '',
      },
      col_right: {
        name: '',
      },
    },
  }

  async changeTimeline(changes, source) {
    if (source === 'edit') {
      await updateScrum(this.props.scrum._id, {
        timeline: this.timelineDetail,
      })
    }
  }
  async changeCheck(changes, source) {
    if (source === 'edit') {
      await updateScrum(this.props.scrum._id, {
        check: this.checkDetail,
      })
    }
  }

  changeScore(value) {
    this.setState({
      score: value,
    })
  }

  async saveScore() {
    const scrum: any = await updateScrum(this.props.scrum._id, {
      scoreList: [
        {
          _id: store.getState().app.id,
          score: this.state.score,
        },
      ],
    })
    const { scoreList } = scrum
    this.setState({
      scoreList,
      score: scoreList ? meanBy(scoreList, 'score').toFixed(1) : '--',
    })
  }

  async selectMembers($event) {
    const _sids = map($event, '_id')
    const dd = await updateScrum(this.props.scrum._id, {
      participants: _sids,
    })
  }

  render() {
    const { summaryDays, score,participants } = this.state

    const content = (
      <div>
        <InputNumber
          min={1}
          max={10}
          onBlur={() => {
            this.saveScore()
          }}
          onChange={(sc) => {
            this.changeScore(sc)
          }}
        />
      </div>
    )
    return (
      <div
        className={`${styles['project-detail-group']} ${styles['project-detail-group-summary']}`}
        style={{ minWidth: summaryDays * 200 }}
      >
        <div
          className={`${styles['project-detail-group-header']} ${styles['project-detail-group-header-summary']}`}
        >
          <div className={styles.left}>
            <div></div>
            <div className="m-t-16">
              <SelectMembersComponent
                selectIds={participants}
                callBack={($event) => {
                  this.selectMembers($event)
                }}
              />
            </div>
          </div>
          <Popover placement="bottom" title={''} content={content} trigger="click">
            <div className={styles.right}>{score}</div>
          </Popover>
        </div>
        <div className={styles['project-detail-group-body']}>
          <div className={styles['project-detail-group-body-summary']}>
            <div className={styles['project-detail-group-body-summary-header']}>
              <RangePicker
                defaultValue={this.summaryRange}
                onChange={(dates, dateStrings) => {
                  this.changeRange(dates, dateStrings)
                }}
              />
            </div>
            <div className={styles['project-detail-group-body-summary-body']}>
              <HotTable
                data={this.timelineDetail}
                colHeaders={this.colHeaders}
                afterChange={(changes, source) => {
                  this.changeTimeline(changes, source)
                }}
                allowInsertRow={true}
                rowHeaders={false}
                colWidths={(summaryDays * 200 - 30) / summaryDays}
                rowHeights={40}
                width="100%"
                height="auto"
                className="htCenter htMiddle"
                manualColumnResize={true}
                manualRowResize={true}
                licenseKey="non-commercial-and-evaluation"
              />
              <div className="m-t-20">
                <HotTable
                  ref={(ref) => {
                    this.getSummaryRef(ref)
                  }}
                  data={this.checkDetail}
                  afterChange={(changes, source) => {
                    this.changeCheck(changes, source)
                  }}
                  allowInsertRow={true}
                  rowHeaders={true}
                  colHeaders={['', '', '', '', '']}
                  contextMenu={this.ctxm}
                  colWidths={(summaryDays * 200 - 80) / 5}
                  rowHeights={40}
                  allowInsertColumn={true}
                  width="100%"
                  className="htCenter htMiddle"
                  manualColumnResize={true}
                  manualRowResize={true}
                  licenseKey="non-commercial-and-evaluation"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
